#content_bin {
    margin-left: 175px;
    padding-top: 15px;
    width: calc( 100vw - 205px );
}
#root {
    height: 100vh;
    overflow-y: scroll;
}

#mob-stats .field,
#mob-stats .field input,
#mob-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label {
    text-align: left;
}

#areas-list .list,
#mobs-list .list {
    text-align: left;
}

#object-stats .field,
#object-stats .field input,
#object-stats .field textarea {
    width: 100%;
    max-width: 100%;
}

.field label button.ui.icon.button {
    padding: 0;
    display: inline;
    background: transparent;
    color: rgba(0,0,0,.87);
}
#dashboard p {
    margin: 0 auto;
    max-width: 900px;
    text-align: center;
}